import { Scheduler } from 'core/utils/Scheduler';
import { Optional } from 'core/utils/types';

const schedulersKey = Symbol('schedulers');

export function Schedule() {
  return function (target: Function, context: ClassMethodDecoratorContext) {
    const sKey = Symbol(String(context.name));
    return function (this: any, ...args: any[]) {
      const caches = (this[schedulersKey] ??= new Map<
        symbol,
        Scheduler<any>
      >()) as Map<symbol, Scheduler<any>>;
      let scheduler: Optional<Scheduler<any>>;
      if (caches.has(sKey)) scheduler = caches.get(sKey);
      else scheduler = new Scheduler(target.apply(this, ...args));

      if (scheduler) scheduler.schedule(args[0]);
    };
  };
}
